import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Button, Container, Grid, Card, CardContent, CardMedia, Box, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import axios from 'axios';
import profilePic from './images/IMG_6494.JPG';

// Dummy project data
const projects = [
  {
    title: 'Secure and Collaborative Machine Learning-Exploring Split-Fed Learning for Fault Detection',
    description: 'The SplitFed learning paradigm, integrating federated and split learning, significantly enhances fault detection in machinery by analyzing impeller images. This research demonstrates that optimal cut layers and communication rounds vary with client configurations, influencing model training efficiency. While SplitFed offers notable privacy advantages and scalability, future work must address data distribution variability, computational demands, and evolving privacy regulations to fully leverage its potential in industrial applications.',
    imageUrl: 'https://via.placeholder.com/500x300?text=Masters+Thesis',
  },
  {
    title: 'Investment Idea Platform',
    description: 'The Investment Ideas Platform (IIP) is a secure web application designed to facilitate the management of investment ideas and products. The primary objective of the project was to develop a user-friendly platform accommodating four distinct user roles: Admin, Fund Administrator, Relation Manager, and Clients/Customers.',
    imageUrl: 'https://via.placeholder.com/500x300?text=Advanced+Web+Solutions',
  },
  {
    title: 'Data Analysis Of UNR-IDD Intrusion Detection Dataset',
    description: 'The project focuses on leveraging machine learning algorithms to analyze and predict outcomes using the UNR-IDD Intrusion Detection Dataset [Tapadhir Das, Osama Abu Hamdan, Raj Shukla, Shamik Sengupta, Engin Arslan, 2022]. The primary goal is to develop predictive models that can efficiently handle large volumes of data, which are otherwise challenging to process manually.',
    imageUrl: 'https://via.placeholder.com/500x300?text=Machine+Learning+and+Data+Mining',
  },
  {
    title: 'AP Cart',
    description: 'A full-stack online platform aimed at providing a seamless shopping experience for fashion enthusiasts. It integrates Java Spring Boot for robust back-end services, Angular for a dynamic and responsive front-end, and MySQL for dependable data management. This comprehensive solution combines user-friendly features such as product management, secure checkout, and order tracking with administrative tools for efficient e-commerce operations.A comprehensive project that integrates various APIs to create a seamless user experience.',
    imageUrl: 'https://via.placeholder.com/500x300?text=Java+FullSatck',
  },
  {
    title: 'Portfolio',
    description: 'The Portfolio Project is a sophisticated web application designed to showcase individual work and achievements using a modern tech stack. The project employs React for a dynamic, interactive front-end experience and Java (typically with Spring Boot) for a robust back-end.',
    imageUrl: 'https://via.placeholder.com/500x300?text=React+Java',
  },
];

const Homepage = () => {
  const [open, setOpen] = useState(false);
  const [contactDetails, setContactDetails] = useState(null);

  const handleClickOpen = () => {
    axios.get('http://localhost:8080/homepage/getProfile') 
      .then(response => {
        setContactDetails(response.data[0]);
        setOpen(true);
        console.log(response.data);
      })
      .catch(error => {
        console.error('There was an error fetching the contact details!', error);
      });
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <AppBar position="static" sx={{ mb: 4 }}>
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            ANUDEEP PALLIYATH
          </Typography>
          <Button color="inherit" onClick={handleClickOpen}>Contact</Button>
        </Toolbar>
      </AppBar>

      <Box sx={{ py: 4, display: 'flex', justifyContent: 'center', alignItems: 'center', backgroundColor: '#e0e0e0' }}>
        <img
          src={profilePic}
          alt="Profile"
          style={{ borderRadius: '50%', border: '3px solid #000', width: '400px', height: '400px' }}
        />
      </Box>

      <Box sx={{ backgroundColor: '#f5f5f5', py: 8 }}>
        <Container maxWidth="md">
          <Typography variant="h2" align="center" sx={{ mb: 2 }}>
            Showcasing My Creative Journey
          </Typography>
          <Typography variant="h5" sx={{ mb: 4 }}>
            MSc Computer Science Graduate
          </Typography>
          <Box sx={{ textAlign: 'center' }}>
            <Button variant="contained" color="primary" onClick={handleClickOpen}>Contact Me</Button>
          </Box>
        </Container>
      </Box>

      <Container sx={{ py: 4 }}>
        <Typography variant="h4" gutterBottom>
          About Me
        </Typography>
        <Typography variant="body1" align="justify" paragraph>
          As a distinguished graduate with a Master's in Computer Science (Distinction) from Anglia Ruskin University, Cambridge, I bring strong skills in software development, data analysis, and machine learning, supported by practical technical support experience. At Poornam Info Vision, I provided technical support for Linux and Windows systems, optimised server performance, and implemented ISO 27001 standards. My data analysis expertise includes applying machine learning for insights and predictive analytics. With experience in full-stack development (ReactJS, Angular, Spring Boot, ASP.NET), I am eager to contribute to innovative projects and impactful results.
        </Typography>
      </Container>

      <Container sx={{ py: 4 }}>
        <Typography variant="h4" gutterBottom>
          Projects
        </Typography>
        <Grid container spacing={4}>
          {projects.map((project, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Card sx={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
                <CardMedia
                  component="img"
                  height="200"
                  image={project.imageUrl}
                  alt={`Project ${index + 1} Image`}
                />
                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography variant="h5" component="div">
                    {project.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                    {project.description}
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>

      <Box sx={{ py: 6, backgroundColor: '#f5f5f5' }}>
        <Container maxWidth="sm">
          <Typography variant="body1" align="center">
             Anudeep Palliyath. All rights reserved.
          </Typography>
        </Container>
      </Box>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Contact Details</DialogTitle>
        <DialogContent>
          {contactDetails ? (
            <>
              <Typography variant="body1">Email: {contactDetails.email}</Typography>
              <Typography variant="body1">Phone: {contactDetails.phone}</Typography>
            </>
          ) : (
            <Typography variant="body1">Loading...</Typography>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default Homepage;
